import React from "react";
import Product from "./bass_item.jsx";

const Bass = ({ bass }) => {
  return (
    <div className="col-md-6 col-lg-4 mb-4">
      <div className="card listing-preview">
        <div className="crd-img">
          <img
            className="card-img-top img-fluid"
            src={bass.pic_url}
            alt="bass"
            style={{ maxWidth: "100%" }}
          />
        </div>
        <div className="card-body">
          <div className="listing-heading text-center">
            <p>
              {bass.manufacturer} {bass.product_name}
            </p>
          </div>
          <p className="card-text">MSRP: ${bass.msrp}</p>
          <a href={bass.url} target="_blank" className="btn btn-primary">
            More Info
          </a>
        </div>
      </div>
    </div>
  );
};

export default Bass;
