import React from "react";

class Product extends React.Component {
  render() {
    return (
      <div className="container">
        <div className="row mx-4">
          <div className="col-md-8">
            <div className="row">
              <div className="d-flex flex-column">
                <h2>{this.props.product.product_name}</h2>
                <h4>{this.props.product.manufacturer}</h4>
              </div>

              <div className="card mb-4">
                <img
                  className="card-img-top"
                  src={this.props.product.pic_url}
                  alt="Bass Image"
                ></img>
                {this.props.product.description && (
                  <div className="card-body">
                    <h3 className="card-title">Description</h3>
                    <p className="card-text">
                      {this.props.product.description}
                    </p>
                  </div>
                )}
                {this.props.product.msrp && (
                  <div className="card-body">
                    <h3 className="card-title">
                      MSRP - ${this.props.product.msrp}
                    </h3>
                  </div>
                )}
                <div className="card-body">
                  <h3 className="card-title">Specifications</h3>
                  <p className="card-text">
                    <ul>
                      <li>
                        <strong>Number of Strings:</strong>{" "}
                        {this.props.product.strings}
                      </li>
                      <li>
                        <strong>Scale Length:</strong>{" "}
                        {this.props.product.scale_length_mm}mm /{" "}
                        {this.props.product.scale_length_in}''
                      </li>
                      <li>
                        <strong>Neck Shape:</strong>{" "}
                        {this.props.product.neck_shape}
                      </li>
                      <li>
                        <strong>Neck Material:</strong>{" "}
                        {this.props.product.neck_material}
                      </li>
                      <li>
                        <strong>Fretboard:</strong>{" "}
                        {this.props.product.fingerboard}
                        {this.props.product.inlays &&
                          `w/ ${this.props.product.inlays} inlays`}
                      </li>
                      <li>
                        <strong>Number of Frets:</strong>{" "}
                        {this.props.product.no_of_frets}
                      </li>
                      <li>
                        <strong>Fret Size:</strong>{" "}
                        {this.props.product.fret_size}
                      </li>
                      <li>
                        <strong>Body Material:</strong>{" "}
                        {this.props.product.body_material}
                      </li>
                      <li>
                        <strong>Body Shape:</strong>{" "}
                        {this.props.product.body_shape}
                      </li>
                      <li>
                        <strong>Pickup Configuration:</strong>{" "}
                        {this.props.product.pup_config}
                      </li>
                      {this.props.product.pup_neck && (
                        <li>
                          <strong>Pickup Neck:</strong>{" "}
                          {this.props.product.pup_config}
                        </li>
                      )}
                      {this.props.product.pup_mid && (
                        <li>
                          <strong>Pickup Middle:</strong>{" "}
                          {this.props.product.pup_mid}
                        </li>
                      )}
                      {this.props.product.pup_brid && (
                        <li>
                          <strong>Pickup Bridge:</strong>{" "}
                          {this.props.product.pup_brid}
                        </li>
                      )}
                      {this.props.product.hardware && (
                        <li>
                          <strong>Hardware:</strong>{" "}
                          {this.props.product.hardware}
                        </li>
                      )}
                    </ul>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Product;
