import "./styles.css";
import Bass from "./bass.jsx";
import basslogo from "./short_scale_bass_black.png";
import React, { useState } from "react";
// Import data from json file
const bassData = require("./data.json");

// Bass list component
const BassList = ({ basses }) => (
  <div className="bass-list">
    {basses.map((bass) => (
      <Bass key={bass.id} bass={bass} />
    ))}
  </div>
);

// Bass filter component
const BassFilter = ({ basses, setFilteredBasses }) => {
  const [manufacturer, setMake] = useState("");
  const [product_name, setModel] = useState("");
  const [price, setPrice] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();

    const filteredBasses = basses.filter((bass) => {
      if (manufacturer && !bass.manufacturer.includes(manufacturer)) {
        return false;
      }
      if (product_name && !bass.product_name.includes(product_name)) {
        return false;
      }
      if (price && !bass.price.includes(price)) {
        return false;
      }
      return true;
    });

    setFilteredBasses(filteredBasses);
  };

  return (
    <form onSubmit={handleSubmit}>
      <input
        type="text"
        placeholder="Manufacturer"
        value={manufacturer}
        onChange={(e) => setMake(e.target.value)}
      />
      <input
        type="text"
        placeholder="Model"
        value={product_name}
        onChange={(e) => setModel(e.target.value)}
      />
      <input
        type="text"
        placeholder="Price"
        value={price}
        onChange={(e) => setPrice(e.target.value)}
      />
      <button type="submit">Filter</button>
    </form>
  );
};

export default function App() {
  const [basses] = useState(bassData);

  const [filteredBasses, setFilteredBasses] = useState(basses);

  return (
    <div className="App">
      <header>
        <h1>
          <a href="/">
            <img
              src={basslogo}
              alt="Bass Logo"
              class="logo"
              width="50"
              height="20"
            />
            ShortScaledBass.com
          </a>
        </h1>
      </header>
      <main id="main" class="pb-4 mt-1 my-4">
        <BassFilter basses={basses} setFilteredBasses={setFilteredBasses} />
        <div class="container">
          <div class="row card-group">
            <BassList basses={filteredBasses} />
          </div>
        </div>
      </main>
      <footer>
        <p>&copy; 2020 Bass Guitars</p>
      </footer>
    </div>
  );
}
